import { AuviousState, State } from '../AuviousState';
import { AuviousStateContainer } from '../AuviousStateContainer';
import { Rule } from '../Rule';
import { Condition } from "../Condition";
import { debug } from "../Debug";
import { anyEvent, apiRequest } from "../Conditions";
import { setupHandler, print } from "../Actions";
import {
    ConnectOptions, RegisterOptions, CallOptions, CreateConferenceOptions,
    Conference, JoinConferenceOptions, ConferenceSession
} from "../AuviousApi";
import { ApiEvent } from "../Event";

import { Promise } from 'es6-promise';

export class BaseAuviousState implements AuviousState {
    stateContainer: AuviousStateContainer;
    rules: Rule[];
    readonly state: string = State.UNKNOWN;

    constructor(stateContainer: AuviousStateContainer) {
        this.stateContainer = stateContainer;
        this.rules = [];
        this.setupRules((condition: Condition): Rule => {
            let rule: Rule = new Rule(condition);
            this.rules.push(rule);
            return rule;
        });
    }

    setupRules(when: Function) {
        debug('base rules setup');

        when(anyEvent())
            .then(print());

        when(apiRequest('on'))
            .then(setupHandler());
    }

    connect(options: ConnectOptions): Promise<void> {
        throw new Error("Invalid State");
    }

    disconnect(): Promise<void> {
        throw new Error("Invalid State");
    }

    register(options: RegisterOptions): Promise<void> {
        throw new Error("Invalid State");
    }

    unregister(): Promise<void> {
        throw new Error("Invalid State");
    }

    connecting() {
        return false;
    }

    connected(): boolean {
        return false;
    }

    registered(): boolean {
        return false;
    }

    identity(): string {
        return this.stateContainer.identity;
    }

    endpoint(): string {
        return this.stateContainer.endpoint;
    }

    // one on one calling
    call(username: string, options?: CallOptions): void {
        throw new Error("invalid state");
    }

    // conference calling
    createConference(options: CreateConferenceOptions): Promise<Conference> {
        throw new Error("invalid state");
    }

    // join conference
    joinConference(options: JoinConferenceOptions): Promise<ConferenceSession> {
        throw new Error("invalid state");
    }

    // message a user
    message(username: string, message: string): Promise<void> {
        throw new Error("invalid state");
    }

    // set event handler
    on(event: string, handler: Function) {
        let apiEvent = new ApiEvent('on', { event: event, handler: handler, stateContainer: this.stateContainer });
        this.stateContainer.handle(apiEvent);
    }

}


