import { BaseConferenceSessionState } from "./BaseConferenceSessionState";
import { ConferenceSessionState } from "../../AuviousState";
import { joinConferenceSessionEvent } from '../../ConferenceConditions';
import {
    switchState, removeFromConferenceSessions,
    sendJoinConferenceEndedEvent
} from '../../ConferenceActions';
import { debug } from "../../Debug";

export class TerminatingConferenceSessionState extends BaseConferenceSessionState {
    state = ConferenceSessionState.TERMINATING;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('terminating conference session state rule setup');

        when(joinConferenceSessionEvent('ended'))
            .then(switchState(ConferenceSessionState.TERMINATED))
            .and(removeFromConferenceSessions())
            .and(sendJoinConferenceEndedEvent())
    }
}


