import { State } from '../AuviousState';
import { ConnectOptions, RegisterOptions } from '../AuviousApi';
import { ApiEvent } from '../Event';
import { apiRequest } from '../Conditions';
import {
    switchState, connect, register,
    setupConnectingPromise, setupRegisteringPromise
} from '../Actions';

import { debug } from '../Debug';
import { BaseAuviousState } from "./BaseAuviousState";
import { Promise } from 'es6-promise';

export class DisconnectedState extends BaseAuviousState {
    readonly state = State.DISCONNECTED;

    setupRules(when: Function): void {
        super.setupRules(when);

        debug('disconnected state rule setup')

        when(apiRequest('connect'))
            .then(switchState(State.CONNECTING))
            .and(setupConnectingPromise())
            .and(connect());

        when(apiRequest('register'))
            .then(switchState(State.REGISTERING))
            .and(setupRegisteringPromise())
            .and(register());
    }

    connect(options: ConnectOptions): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            let event = new ApiEvent('connect', {
                options: options, stateContainer: this.stateContainer,
                resolve: resolve, reject: reject
            });
            this.stateContainer.handle(event);
        })
    }

    register(options: RegisterOptions): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            let event = new ApiEvent('register', {
                options: options, stateContainer: this.stateContainer,
                resolve: resolve, reject: reject
            });
            this.stateContainer.handle(event);
        })
    }
}


