import { AuviousConferenceSessionState, ConferenceSessionState } from "../../AuviousState";
import { Conference, ConferenceSessionEventHandlers, PublishOptions, Stream, Endpoint } from "../../AuviousApi";
import { Rule } from "../../Rule";
import { Condition } from "../../Condition";
import { debug } from "../../Debug";
import { anyEvent } from "../../Conditions";
import { ConferenceSessionImplFsm } from "../../ConferenceSessionImplFsm";
import { print } from '../../Actions';

import { Promise } from 'es6-promise';

export class BaseConferenceSessionState implements AuviousConferenceSessionState {
    fsm: ConferenceSessionImplFsm;

    // StateWithRules
    readonly state: string = ConferenceSessionState.UNKNOWN;
    rules: Rule[];

    constructor(fsm: ConferenceSessionImplFsm) {
        this.fsm = fsm;

        this.rules = [];
        this.setupRules((condition: Condition): Rule => {
            let rule: Rule = new Rule(condition);
            this.rules.push(rule);
            return rule;
        });
    }

    setupRules(when: Function): void {
        debug('base conference session rules setup');

        when(anyEvent())
            .then(print());
    }

    get session(): any {
        return this.fsm.conferenceSession.session;
    }

    // ConferenceSession
    get id(): string {
        return this.fsm.conferenceSession.id;
    }

    get conference(): Conference {
        return this.fsm.conferenceSession.conference;
    }

    get eventHandlers(): ConferenceSessionEventHandlers {
        return this.fsm.conferenceSession.eventHandlers;
    }

    get direction(): string {
        return !!this.session ? this.session.direction : "unknown";
    }

    get remoteEndpoint(): Endpoint {
        return this.fsm.conferenceSession.remoteEndpointData;
    }

    isInProgress(): boolean {
        return !!this.session && this.session.isInProgress();
    }

    isEstablished(): boolean {
        return !!this.session && this.session.isEstablished();
    }

    publish(options: PublishOptions): Promise<Stream> {
        throw new Error('Invalid State.');
    }

    unpublish(stream: Stream) {
        throw new Error('Invalid State.');
    }

    leave(): void {
        throw new Error('Invalid State.');
    }

    terminate(): void {
        throw new Error('Invalid State.');
    }

    on(eventId: string, handler: Function) {
        this.eventHandlers[eventId] = handler;
    }

}


