import { Promise } from 'es6-promise';
import * as JsSIP from 'jssip';

export const jssip: any = JsSIP;

export interface UAEventHandlers {
    connecting?: Function;
    connected?: Function;
    disconnected?: Function;
    connectionFailed?: Function;
    registered?: Function;
    unregistered?: Function;
    registrationFailed?: Function;
    warning?: Function;
    newSession?: Function;
    newConferenceSession?: Function;
    conferenceCreated?: Function;
    createConferenceFailed?: Function;
    newMessage?: Function;
}

export interface SessionEventHandlers {
    connecting?: Function;
    ringing?: Function;
    accepted?: Function;
    streamAdded?: Function;
    streamRemoved?: Function;
    ended?: Function;
    failed?: Function;
}

export interface ConferenceSessionEventHandlers {
    connecting?: Function;
    accepted?: Function;
    endpointJoined?: Function;
    endpointLeft?: Function;
    streamAdded?: Function;
    streamRemoved?: Function;
    ended?: Function;
    failed?: Function;
}

export interface ConnectOptions {
    username: string;
    focusUrl?: string;
    eventHandlers?: UAEventHandlers;
}

export interface RegisterOptions {
    username: string;
    password?: string;
    focusUrl?: string;
    eventHandlers?: UAEventHandlers;
    body?: string;
    contentType?: string
}

export interface MediaConstraints {
    audio?: boolean;
    video?: boolean;
}

export interface CallOptions {
    mediaStream?: MediaStream;
    mediaConstraints?: MediaConstraints;
    receiveMediaConstraints?: MediaConstraints;
    eventHandlers?: SessionEventHandlers;
}

export interface AnswerOptions {
    mediaStream?: MediaStream;
    mediaConstraints?: MediaConstraints;
    receiveMediaConstraints?: MediaConstraints;
    eventHandlers?: SessionEventHandlers;
}

export interface HoldInfo {
    local?: boolean;
    remote?: boolean;
}

export interface Message {
    content: string;
    contentType: string;
    sender: string;
    recipient: string;
}

export interface Session {
    id: string;
    eventHandlers: SessionEventHandlers;
    readonly direction: string;
    readonly remoteEndpoint: Endpoint;
    isInProgress(): boolean;
    isEstablished(): boolean;
    isOnHold(): HoldInfo;
    hold(): Promise<void>;
    unhold(): Promise<void>;
    answer(options?: AnswerOptions): void;
    terminate(): void;
    on(eventId: string, handler: Function);
}

export interface PublishOptions {
    mediaStream?: MediaStream;
    mediaConstraints?: MediaConstraints;
    type: string;
}

export interface ConferenceSession {
    id: string;
    conference: Conference;
    eventHandlers: ConferenceSessionEventHandlers;
    readonly direction: string;
    readonly remoteEndpoint: Endpoint;
    isInProgress(): boolean;
    isEstablished(): boolean;
    publish(options: PublishOptions): Promise<Stream>;
    unpublish(stream: Stream);
    leave(): void;
    terminate(): void;
    on(eventId: string, handler: Function);
}

export class ConferenceMode {
    static P2P: string = "p2p";
    static ROUTER: string = "router";
    static UNKNOWN: string = "unknown"
}

export interface Conference {
    name: string;
    mode: string;
}

export interface CreateConferenceOptions {
    id?: string;
    mode?: string;
}

export interface JoinConferenceOptions {
    id: string;
    eventHandlers: ConferenceSessionEventHandlers;
}

export interface Endpoint {
    username: string;
    endpoint: string;
}

export class StreamType {
    static MIC: string = "mic";
    static CAM: string = "cam";
    static SCREEN: string = "screen";
    static VIDEO: string = "video";
    static UNKNOWN: string = "unknown";
}

export interface Stream {
    id: string;
    mediaStream?: MediaStream;
    type: string;
    originator: Endpoint;
}

export interface AuviousApi {
    // for guest/non registered users
    connect(options: ConnectOptions): Promise<void>;
    disconnect(): Promise<void>;

    // registered users use this
    register(options: RegisterOptions): Promise<void>;
    unregister(): Promise<void>;

    connecting(): boolean;
    connected(): boolean;
    registered(): boolean;

    identity(): string;
    endpoint(): string;

    // one on one calling
    call(username: string, options?: CallOptions): void;

    // conference calling
    createConference(options?: CreateConferenceOptions): Promise<Conference>;

    // join conference
    joinConference(options: JoinConferenceOptions): Promise<ConferenceSession>;

    // message a user
    message(username: string, message: string): Promise<void>;

    // set event handler
    on(event: string, handler: Function);
}


