import { Stream, StreamType, Endpoint } from './AuviousApi';

export class StreamImpl implements Stream {
    id: string;
    type: string;
    originator: Endpoint;
    mediaStream?: MediaStream;
    session?: any; // jssip publish/view RTCSession
    p2pSessions?: any; // jssip incoming p2p RTCSession key,value => Endpoint, RTCSession
    sipUrl?: any;

    constructor(id: string, type: string, originator: Endpoint) {
        this.id = id;
        this.type = type;
        this.originator = originator;
    }

    terminate(): void {
        // first terminate p2p sessions
        if (!!this.p2pSessions) {
            Object.keys(this.p2pSessions).forEach(endpoint => {
                let session = this.p2pSessions[endpoint];
                if (session.isEstablished()) {
                    session.terminate();
                }
            });
        }

        // finally terminate publish or view session.
        if (!!this.session && this.session.isEstablished()) {
            this.session.terminate();
            return;
        }
    }
}


