import {
    Session, AnswerOptions, HoldInfo, SessionEventHandlers,
    Endpoint, Stream
} from './AuviousApi';
import { JSSIP_UA_EVENTS, JSSIP_SESSION_EVENTS, ICE_SERVERS } from './JSSIP';
import { uuid, uriToEndpoint } from './Common';
import { debug } from './Debug';
import { Event } from './Event';
import { SessionState, AuviousSessionState } from "./AuviousState";

import { SessionImplFsm } from "./SessionImplFsm";

import { Promise } from 'es6-promise';

export class SessionImpl implements Session {
    id: string;

    session: any; //jssip Session
    eventHandlers: SessionEventHandlers
    remoteEndpointData: Endpoint;
    localStream: Stream;
    remoteStream: Stream;

    fsm: SessionImplFsm;

    constructor(session: any, eventHandlers?: SessionEventHandlers) {
        this.id = uuid();
        this.session = session;

        if (!!eventHandlers) {
            this.eventHandlers = eventHandlers;
        } else {
            this.eventHandlers = {};
        }

        if (!!session) {
            this.remoteEndpointData = { username: session.remote_identity.uri.user, endpoint: null };
        }

        if (!!session && session.direction == 'incoming') {
            this.remoteEndpointData.endpoint = uriToEndpoint(session.request.headers['Contact'][0].parsed.uri);
        }

        this.fsm = new SessionImplFsm(this);
    }

    get direction() {
        return this.fsm.direction;
    }

    get remoteEndpoint(): Endpoint {
        return this.fsm.remoteEndpoint;
    }

    isInProgress(): boolean {
        return this.fsm.isInProgress();
    }

    isEstablished(): boolean {
        return this.fsm.isEstablished();
    }

    isOnHold(): HoldInfo {
        return this.fsm.isOnHold();
    }

    hold(): Promise<void> {
        return this.fsm.hold();
    }

    unhold(): Promise<void> {
        return this.fsm.unhold();
    }

    answer(options?: AnswerOptions): void {
        this.fsm.answer(options);
    }

    terminate(): void {
        this.fsm.terminate();
    }

    on(eventId: string, handler: Function) {
        this.fsm.on(eventId, handler);
    }
}


