import { Condition } from './Condition';
import { AuviousStateContainer } from './AuviousStateContainer';
import {
    Event, ApiEvent, CreateConferenceSessionEvent, JoinConferenceSessionEvent,
    NotifyEvent, PublishStreamSessionEvent, ViewStreamSessionEvent,
    ConferenceXmlEndpointEvent, ConferenceXmlStreamEvent, P2PStreamSessionEvent
} from './Event';
import { ConferenceSessionImpl } from './ConferenceSessionImpl';

export function anyCreateConferenceSessionEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof CreateConferenceSessionEvent;
        }
    });
}

export function createConferenceSessionEvent(eventId: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof CreateConferenceSessionEvent)) {
                return false;
            }

            return event.eventId == eventId;
        }
    });
}

export function createConferenceSessionRedirected(value: boolean): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof CreateConferenceSessionEvent)) {
                return false;
            }

            let result: boolean = (event.eventId == 'failed' && event.eventData.message.status_code == 302);

            return result == value;
        }
    });
}

export function anyJoinConferenceSessionEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof JoinConferenceSessionEvent;
        }
    });
}

export function joinConferenceSessionEvent(eventId: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof JoinConferenceSessionEvent)) {
                return false;
            }

            return eventId == event.eventId;
        }
    });
}

export function joinConferenceSessionOriginator(originator: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof JoinConferenceSessionEvent)) {
                return false;
            }

            return event.eventData.originator == originator;
        }
    })
}

export function notified(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof NotifyEvent;
        }
    })
}

export function notifiedForTheFirstTime(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof NotifyEvent)) {
                return false;
            }

            let session = event.session as ConferenceSessionImpl;

            return session.notifications == 0;
        }
    })
}

export function anyPublishStreamSessionEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof PublishStreamSessionEvent;
        }
    });
}

export function anyViewStreamSessionEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof ViewStreamSessionEvent;
        }
    });
}

export function anyConferenceXmlEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return (event instanceof ConferenceXmlEndpointEvent) || (event instanceof ConferenceXmlStreamEvent);
        }
    })
}

export function conferenceXmlEndpointEvent(eventId: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof ConferenceXmlEndpointEvent && event.eventId == eventId;
        }
    })
}

export function conferenceXmlStreamEvent(eventId: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof ConferenceXmlStreamEvent && event.eventId == eventId;
        }
    })
}

export function conferenceXmlStreamEventIsNotMe(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof ConferenceXmlStreamEvent && event.stream.originator.endpoint != event.session.stateContainer.endpoint;
        }
    })
}

export function conferenceXmlEndpointEventIsNotMe(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof ConferenceXmlEndpointEvent
                && event.endpoint.endpoint != event.session.stateContainer.endpoint;
        }
    })
}

export function publishStreamSessionEvent(eventId: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof PublishStreamSessionEvent && event.eventId == eventId;
        }
    });
}

export function viewStreamSessionEvent(eventId: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof ViewStreamSessionEvent && event.eventId == eventId;
        }
    });
}

export function anyP2PStreamSessionEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof P2PStreamSessionEvent;
        }
    })
}

export function p2pStreamSessionEvent(eventId: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof P2PStreamSessionEvent && event.eventId == eventId;
        }
    })
}




