import { BaseConferenceSessionState } from "./BaseConferenceSessionState";
import { ConferenceSessionState } from "../../AuviousState";
import { debug } from "../../Debug";
import { joinConferenceSessionEvent, joinConferenceSessionOriginator } from "../../ConferenceConditions";
import {
    sendJoinConferenceAcceptedEvent, switchState, sendJoinConferenceFailedEvent,
    subscribeToConference, updateConferenceSessionConference, updateConferenceSessionEndpointData
} from "../../ConferenceActions";

export class InProgressConferenceSessionState extends BaseConferenceSessionState {
    state = ConferenceSessionState.IN_PROGRESS;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('in progress conference session state rule setup');

        when(joinConferenceSessionEvent('accepted').and(joinConferenceSessionOriginator('remote')))
            .then(updateConferenceSessionConference())
            .and(updateConferenceSessionEndpointData())
            .and(sendJoinConferenceAcceptedEvent());

        when(joinConferenceSessionEvent('confirmed'))
            .then(switchState(ConferenceSessionState.ESTABLISHED))
            .and(subscribeToConference());

        when(joinConferenceSessionEvent('failed'))
            .then(switchState(ConferenceSessionState.FAILED))
            .and(sendJoinConferenceFailedEvent())
    }
}


