import { ConferenceSession, Conference, ConferenceSessionEventHandlers, PublishOptions, Stream, Endpoint } from "./AuviousApi";
import { EventHandler } from "./EventHandler";
import { Event } from "./Event";
import { ConferenceSessionImpl } from "./ConferenceSessionImpl";
import { AuviousConferenceSessionState, ConferenceSessionState } from "./AuviousState";
import { InitializedConferenceSessionState } from './states/conferenceSession/InitializedConferenceSessionState';
import { InProgressConferenceSessionState } from './states/conferenceSession/InProgressConferenceSessionState';
import { EstablishedConferenceSessionState } from './states/conferenceSession/EstablishedConferenceSessionState';
import { TerminatingConferenceSessionState } from './states/conferenceSession/TerminatingConferenceSessionState';
import { TerminatedConferenceSessionState } from './states/conferenceSession/TerminatedConferenceSessionState';
import { FailedConferenceSessionState } from './states/conferenceSession/FailedConferenceSessionState';

import { Promise } from 'es6-promise';

export class ConferenceSessionImplFsm implements ConferenceSession, EventHandler {
    conferenceSession: ConferenceSessionImpl;
    state: AuviousConferenceSessionState;
    states: AuviousConferenceSessionState[];

    constructor(conferenceSession: ConferenceSessionImpl) {
        this.conferenceSession = conferenceSession;

        this.states = [
            new InitializedConferenceSessionState(this),
            new InProgressConferenceSessionState(this),
            new EstablishedConferenceSessionState(this),
            new TerminatedConferenceSessionState(this),
            new TerminatingConferenceSessionState(this),
            new FailedConferenceSessionState(this)
        ];

        this.state = this.getState(ConferenceSessionState.INITIALIZED);
    }

    get id(): string {
        return this.state.id;
    }

    get conference(): Conference {
        return this.state.conference;
    }

    get eventHandlers(): ConferenceSessionEventHandlers {
        return this.state.eventHandlers;
    }

    get direction(): string {
        return this.state.direction;
    }

    get remoteEndpoint(): Endpoint {
        return this.state.remoteEndpoint;
    }

    isInProgress(): boolean {
        return this.state.isInProgress();
    }

    isEstablished(): boolean {
        return this.state.isEstablished();
    }

    publish(options: PublishOptions): Promise<Stream> {
        return this.state.publish(options);
    }

    unpublish(stream: Stream) {
        this.state.unpublish(stream);
    }

    leave(): void {
        this.state.leave();
    }

    terminate(): void {
        this.state.terminate();
    }

    on(eventId: string, handler: Function) {
        this.state.on(eventId, handler);
    }

    // EventHandler
    handle(event: Event): void {
        this.state.rules.forEach(rule => rule.process(event));
    }

    // utility functions
    getState(state: string): AuviousConferenceSessionState {
        let retval: AuviousConferenceSessionState;

        for (let i = 0; i < this.states.length; i++) {
            if (this.states[i].state == state) {
                retval = this.states[i];
                break;
            }
        }

        return retval;
    }

    switchState(state: string): void {
        let newState = this.getState(state);

        if (!newState) {
            throw new Error(`invalid state: ${state}`)
        }
        this.state = this.getState(state);
    }

}


