import { State } from "../AuviousState";
import { debug } from "../Debug";
import { uaEvent, connectionFailed, registrationFailed } from "../Conditions";
import {
    switchState, sendConnectingEvent, sendConnectedEvent,
    updateIdentity, updateEndpoint, sendRegisteredEvent,
    stopPhone, sendConnectionFailedEvent,
    resolveRegisterPromise, rejectRegisterPromise,
    sendRegistrationFailedEvent
} from "../Actions";
import { BaseAuviousState } from "./BaseAuviousState";

export class RegisteringState extends BaseAuviousState {
    readonly state = State.REGISTERING;
    resolve: Function;
    reject: Function;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('registering state rule setup')

        when(uaEvent('connecting'))
            .and(sendConnectingEvent());
        when(uaEvent('connected'))
            .and(sendConnectedEvent());
        when(uaEvent('registered'))
            .then(switchState(State.REGISTERED))
            .and(updateIdentity())
            .and(updateEndpoint())
            .and(resolveRegisterPromise())
            .and(sendRegisteredEvent())

        // error handling
        when(connectionFailed())
            .then(switchState(State.DISCONNECTED))
            .and(stopPhone())
            .and(rejectRegisterPromise())
            .and(sendConnectionFailedEvent())

        when(registrationFailed())
            .then(switchState(State.DISCONNECTED))
            .and(stopPhone())
            .and(rejectRegisterPromise())
            .and(sendRegistrationFailedEvent())
    }

}


