import {
    CallOptions, CreateConferenceOptions, Conference,
    JoinConferenceOptions, ConferenceSession
} from "../AuviousApi";
import { State } from "../AuviousState";
import { ApiEvent } from '../Event';

import { Promise } from 'es6-promise';
import { debug } from "../Debug";
import {
    apiRequest, uaEvent, newOutgoingSession, newIncomingSession,
    newIncomingConferenceP2PStreamSession, connectionFailed,
    newMessage
} from "../Conditions";
import { createConferenceSessionEvent, createConferenceSessionRedirected } from '../ConferenceConditions';
import {
    switchState, stopPhone, sendNewSessionEvent, call, setupNewRTCSession,
    forwardConferenceP2PStreamSessionToConferenceSession, sendConnectionFailedEvent,
    setupUnregisteringPromise, message, sendNewMessageEvent
} from "../Actions";
import {
    createConference, joinConference, sendNewConferenceSessionEvent,
    sendConferenceCreatedEventAndResolvePromise, sendConferenceCreationFailedAndRejectPromise, addToConferenceSessions
} from "../ConferenceActions";
import { BaseAuviousState } from "./BaseAuviousState";

export class RegisteredState extends BaseAuviousState {
    readonly state = State.REGISTERED;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('registered state rule setup')

        // api requests
        when(apiRequest('unregister'))
            .then(switchState(State.UNREGISTERING))
            .and(setupUnregisteringPromise())
            .and(stopPhone());

        when(apiRequest('call'))
            .then(call())

        when(apiRequest('message'))
            .then(message())

        when(newMessage())
            .then(sendNewMessageEvent())

        when(apiRequest('createConference'))
            .then(createConference());

        when(apiRequest('joinConference'))
            .then(joinConference())
            .and(addToConferenceSessions())
            .and(sendNewConferenceSessionEvent());

        // jssip phone events
        when(newIncomingSession().or(newOutgoingSession()))
            .then(setupNewRTCSession())
            .and(sendNewSessionEvent())

        when(newIncomingConferenceP2PStreamSession())
            .then(forwardConferenceP2PStreamSessionToConferenceSession())

        // jssip create conference session events
        when(createConferenceSessionEvent('failed').and(createConferenceSessionRedirected(true)))
            .then(sendConferenceCreatedEventAndResolvePromise())
        when(createConferenceSessionEvent('failed').and(createConferenceSessionRedirected(false)))
            .then(sendConferenceCreationFailedAndRejectPromise());

        // error handling
        when(connectionFailed())
            .then(switchState(State.DISCONNECTED))
            .and(stopPhone())
            .and(sendConnectionFailedEvent())

        /*
        when(connectionFailed())
            .then(switchState(State.CONNECTION_FAILED))
            .and(retryConnection())
        */

        // connection failed state setupRules
        /*when(uaEvent('registered'))
            .then(restoreSessions())
            .and(restoreConferenceSessions())
        */
    }

    unregister(): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            let event = new ApiEvent('unregister', {
                stateContainer: this.stateContainer,
                resolve: resolve, reject: reject
            });
            this.stateContainer.handle(event);
        })
    }

    connected(): boolean {
        return true;
    }

    registered(): boolean {
        return true;
    }

    // one on one calling
    call(username: string, options?: CallOptions): void {
        let event = new ApiEvent('call', { username: username, options: options, stateContainer: this.stateContainer });
        this.stateContainer.handle(event);
    }

    // conference calling
    createConference(options?: CreateConferenceOptions): Promise<Conference> {
        return new Promise<Conference>((resolve, reject) => {
            let event = new ApiEvent('createConference', {
                options: options,
                resolve: resolve,
                reject: reject,
                stateContainer: this.stateContainer
            });
            this.stateContainer.handle(event);
        });
    }

    // join conference
    joinConference(options: JoinConferenceOptions): Promise<ConferenceSession> {
        return new Promise<ConferenceSession>((resolve, reject) => {
            let event = new ApiEvent('joinConference', {
                options: options,
                resolve: resolve,
                reject: reject,
                stateContainer: this.stateContainer
            });
            this.stateContainer.handle(event);
        });
    }

    // message a user
    message(username: string, message: string): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            let event = new ApiEvent('message', {
                username: username,
                message: message,
                resolve: resolve,
                reject: reject,
                stateContainer: this.stateContainer
            });
            this.stateContainer.handle(event);
        });
    }
}


