import {
    AuviousApi, ConnectOptions, RegisterOptions, CallOptions,
    ConferenceSession, ConferenceMode, Conference, StreamType, Stream,
    JoinConferenceOptions, CreateConferenceOptions, Session, SessionEventHandlers, HoldInfo, AnswerOptions
} from './AuviousApi';
import { AuviousStateContainer } from './AuviousStateContainer';
import { ApiEvent } from './Event';
import { Rule } from './Rule';
import { Condition } from './Condition';
import { apiRequest, anyEvent } from './Conditions';
import { setupHandler, print } from './Actions';

import { debug } from './Debug';

import { Promise } from 'es6-promise';
import { SessionImpl } from "./SessionImpl";

export class State {
    static CONNECTING: string = "connecting";
    static CONNECTED: string = "connected";
    static DISCONNECTING: string = "disconnecting";
    static DISCONNECTED: string = "disconnected";
    static REGISTERING: string = "registering";
    static REGISTERED: string = "registered";
    static UNREGISTERING: string = "unregistering";
    static UNREGISTERED: string = "unregistered";
    static UNKNOWN: string = "unknown";
}

export interface StateWithRules {
    state: string;
    rules: Rule[];
    setupRules(when: Function): void;
}

export interface AuviousState extends AuviousApi, StateWithRules { }

// Session
export class SessionState {
    static INITIALIZED: string = "initialized";
    static IN_PROGRESS: string = "in_progress";
    static ESTABLISHED: string = "established";
    static TERMINATED: string = "terminated";
    static FAILED: string = "failed";
    static UNKNOWN: string = "unknown";
}

export interface AuviousSessionState extends Session, StateWithRules { }

// ConferenceSession
export class ConferenceSessionState {
    static INITIALIZED: string = "initialized";
    static IN_PROGRESS: string = "in_progress";
    static ESTABLISHED: string = "established";
    static TERMINATING: string = "terminating";
    static TERMINATED: string = "terminated";
    static FAILED: string = "failed";
    static UNKNOWN: string = "unknown";
}

export interface AuviousConferenceSessionState extends ConferenceSession, StateWithRules { }


