import { State } from '../AuviousState';
import { BaseAuviousState } from './BaseAuviousState';
import {
    CallOptions, CreateConferenceOptions, Conference,
    JoinConferenceOptions, ConferenceSession
} from '../AuviousApi';
import { ApiEvent } from '../Event';

import { Promise } from 'es6-promise';
import { debug } from "../Debug";
import { apiRequest, uaEvent, connectionFailed } from "../Conditions";
import {
    switchState, stopPhone, call, sendNewSessionEvent,
    setupNewRTCSession, sendConnectionFailedEvent,
    setupDisconnectingPromise, message
} from "../Actions";
import { createConference, joinConference, sendNewConferenceSessionEvent } from '../ConferenceActions';

export class ConnectedState extends BaseAuviousState {
    readonly state = State.CONNECTED;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('connected state rule setup')

        // api requests
        when(apiRequest('disconnect'))
            .then(switchState(State.DISCONNECTING))
            .and(setupDisconnectingPromise())
            .and(stopPhone());

        when(apiRequest('call'))
            .and(call())

        when(apiRequest('message'))
            .then(message())

        when(apiRequest('createConference'))
            .then(createConference());

        when(apiRequest('joinConference'))
            .and(joinConference())
            .and(sendNewConferenceSessionEvent());

        // jssip phone events
        when(uaEvent('newRTCSession'))
            .then(setupNewRTCSession())
            .then(sendNewSessionEvent())

        // error handling
        when(connectionFailed())
            .then(switchState(State.DISCONNECTED))
            .and(stopPhone())
            .and(sendConnectionFailedEvent())
    }

    disconnect(): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            let event = new ApiEvent('disconnect', {
                stateContainer: this.stateContainer,
                resolve: resolve, reject: reject
            });
            this.stateContainer.handle(event);
        });
    }

    connected(): boolean {
        return true;
    }

    // one on one calling
    call(username: string, options?: CallOptions): void {
        let event = new ApiEvent('call', { username: username, options: options, stateContainer: this.stateContainer });
        this.stateContainer.handle(event);
    }

    // conference calling
    createConference(options: CreateConferenceOptions): Promise<Conference> {
        return new Promise<Conference>((resolve, reject) => {
            let event = new ApiEvent('createConference', {
                options: options,
                resolve: resolve,
                reject: reject,
                stateContainer: this.stateContainer
            });
            this.stateContainer.handle(event);
        });
    }

    // join conference
    joinConference(options: JoinConferenceOptions): Promise<ConferenceSession> {
        return new Promise<ConferenceSession>((resolve, reject) => {
            let event = new ApiEvent('joinConference', {
                options: options,
                resolve: resolve,
                reject: reject,
                stateContainer: this.stateContainer
            });
            this.stateContainer.handle(event);
        });
    }

    // message a user
    message(username: string, message: string): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            let event = new ApiEvent('message', {
                username: username,
                message: message,
                resolve: resolve,
                reject: reject,
                stateContainer: this.stateContainer
            });
            this.stateContainer.handle(event);
        });
    }

}


