import { Session, HoldInfo, AnswerOptions, SessionEventHandlers, Endpoint } from "./AuviousApi";
import { SessionImpl } from "./SessionImpl";
import { InitializedSessionState } from './states/session/InitializedSessionState';
import { InProgressSessionState } from './states/session/InProgressSessionState';
import { EstablishedSessionState } from './states/session/EstablishedSessionState';
import { TerminatedSessionState } from './states/session/TerminatedSessionState';
import { FailedSessionState } from './states/session/FailedSessionState';
import { EventHandler } from "./EventHandler";
import { Event } from './Event';
import { SessionState, AuviousSessionState } from "./AuviousState";
import { debug } from './Debug';

import { Promise } from 'es6-promise';

export class SessionImplFsm implements Session, EventHandler {
    session: SessionImpl;
    state: AuviousSessionState;
    states: AuviousSessionState[];

    constructor(session: SessionImpl) {
        this.session = session;

        this.states = [
            new InitializedSessionState(this),
            new InProgressSessionState(this),
            new EstablishedSessionState(this),
            new TerminatedSessionState(this),
            new FailedSessionState(this)
        ];

        this.state = this.getState(SessionState.INITIALIZED);
    }
    
    // Session
    get id(): string {
        return this.state.id;
    }

    get eventHandlers(): SessionEventHandlers {
        return this.state.eventHandlers;
    }

    get direction(): string {
        return this.state.direction
    }

    get remoteEndpoint(): Endpoint {
        return this.state.remoteEndpoint;
    }

    isInProgress(): boolean {
        return this.state.isInProgress();
    }

    isEstablished(): boolean {
        return this.state.isEstablished();
    }

    isOnHold(): HoldInfo {
        return this.state.isOnHold();
    }

    hold(): Promise<void> {
        return this.state.hold();
    }

    unhold(): Promise<void> {
        return this.state.unhold();
    }
    
    answer(options?: AnswerOptions): void {
        this.state.answer(options);
    }
    
    terminate(): void {
        this.state.terminate();
    }

    on(eventId: string, handler: Function) {
        this.state.on(eventId, handler);
    }

    // EventHandler
    handle(event: Event): void {
        this.state.rules.forEach(rule => rule.process(event));
    }

    // utility functions
    getState(state: string): AuviousSessionState {
        let retval: AuviousSessionState;

        for (let i = 0; i < this.states.length; i++) {
            if (this.states[i].state == state) {
                retval = this.states[i];
                break;
            }
        }

        return retval;
    }

    switchState(state: string): void {
        debug('switchState: ' + state);
        let newState = this.getState(state);

        if (!newState) {
            throw new Error(`invalid state: ${state}`)
        }
        this.state = this.getState(state);
    }
}


