export function subscribeToConference(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof JoinConferenceSessionEvent)) {
                throw new Error("Invalid Event");
            }

            let session = event.session as ConferenceSessionImpl;
            let resolve: Function = event.resolve;
            let reject: Function = event.reject;

            let uri = session.session.remote_identity.uri.toString();
            let eventHandlers = {
                notify: (data) => {
                    let event = new NotifyEvent('notify', data, session, resolve, reject);
                    session.fsm.handle(event);
                }
            }

            subscribe(session, 3600, eventHandlers);
        }
    }
}

