export function publishStream(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof ApiEvent)) {
                throw new Error("Invalid Event");
            }
            let session = event.data().session as ConferenceSessionImpl;
            let mediaStream = event.data().options.mediaStream as MediaStream;
            let mediaConstraints = event.data().options.mediaConstraints as MediaConstraints;
            let type = event.data().options.type as string;
            let resolve = event.data().resolve as Function;
            let reject = event.data().reject as Function;

            let endpoint: Endpoint = { username: session.stateContainer.identity, endpoint: session.stateContainer.endpoint };
            let stream: StreamImpl = new StreamImpl(uuid(), type, endpoint);
            event.extra.stream = stream;

            let mediaStreamPromise = new Promise<MediaStream>((resolve, reject) => {
                if (!!mediaStream) {
                    resolve(mediaStream);
                } else if (!!mediaConstraints) {
                    let getUserMedia = getTemasysGetUserMedia();
                    getUserMedia(mediaConstraints)
                        .then(mediaStream => resolve(mediaStream))
                        .catch(error => reject(error));
                } else {
                    reject(new Error("invalid media parameters"));
                }
            });

            mediaStreamPromise.then(mediaStream => {
                stream.mediaStream = mediaStream;
                if (session.conference.mode == ConferenceMode.P2P) {
                    publishStreamToP2PConference(session, stream, resolve, reject);
                } else {
                    publishStreamToRouterConference(session, stream, resolve, reject);
                }
            });
        }
    }
}

export function unpublishStream(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof ApiEvent)) {
                throw new Error("Invalid Event");
            }

            let stream = event.data().stream as StreamImpl;

            if (!!stream.session) {
                stream.session.terminate();
            } else {
                debug("session not found in stream", stream);
            }
        }
    }
}

