export function addToSessionStreams(): Action {
    return new class {
        run(event: Event): void {
            if (event instanceof ConferenceXmlStreamEvent) {
                let session = event.session;
                let stream = event.stream;

                session.streams[stream.id] = stream;
                return;
            }

            if (event instanceof ApiEvent) {
                let session = event.data().session as ConferenceSessionImpl;
                let stream = event.extra.stream as StreamImpl;

                session.streams[stream.id] = stream;
                return;
            }

            throw new Error("Invalid Event");
        }
    }
}

