export function connectToStream(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof ConferenceXmlStreamEvent)) {
                throw new Error("Invalid Event");
            }

            let session = event.session;
            let stream = event.stream;

            let eventHandlers = [];

            JSSIP_SESSION_EVENTS.forEach(eventId => eventHandlers[eventId] =
                function (eventData: any) {
                    if (!stream.session) {
                        stream.session = this;
                    }
                    session.fsm.handle(new ViewStreamSessionEvent(eventId, eventData, session, stream));
                });

            let rtcOfferConstraints = getCrossBrowserRtcOfferConstraints({
                offerToReceiveAudio: stream.type == StreamType.MIC || stream.type == StreamType.VIDEO ? 1 : 0,
                offerToReceiveVideo: stream.type == StreamType.CAM || stream.type == StreamType.VIDEO || stream.type == StreamType.SCREEN ? 1 : 0
            });

            let options = {
                extraHeaders: [
                    `${ICE_EXCHANGE_HEADER}: async`
                ],
                mediaConstraints: {
                    audio: false,
                    video: false
                },
                eventHandlers: eventHandlers,
                pcConfig: {
                    iceServers: ICE_SERVERS
                },
                rtcOfferConstraints: rtcOfferConstraints
            }

            if (session.conference.mode == ConferenceMode.P2P) {
                options.extraHeaders.push(`${P2P_STREAM_ID_HEADER}: ${stream.id}`);
            }

            debug('connectToStream call options: ', options)

            session.stateContainer.phone.call(stream.sipUrl, options)
        }
    }
}

