export function answerP2PStreamSession(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof UAEvent)) {
                throw new Error("Invalid Event");
            }
            let stateContainer = event.stateContainer;
            let eventData = event.eventData;
            let id = eventData.request.headers[P2P_STREAM_ID_HEADER][0].raw;
            let endpoint = eventData.request.headers['Contact'][0].parsed.uri.toAor();
            let session = getConferenceSession(stateContainer, id);
            let stream = session.streams[id] as StreamImpl;
            let rtcSession = eventData.session;

            if (!stream.p2pSessions) {
                stream.p2pSessions = new Object();
            }

            stream.p2pSessions[endpoint] = rtcSession;

            JSSIP_SESSION_EVENTS.forEach(eventId => rtcSession.on(eventId,
                function (eventData: any) {
                    session.fsm.handle(new P2PStreamSessionEvent(eventId, eventData, session, stream, endpoint));
                }));

            let options = {
                mediaStream: stream.mediaStream,
                pcConfig: {
                    iceServers: ICE_SERVERS
                }
            }
            rtcSession.answer(options);
        }
    }
}

