// sends streamRemoved, endpointRemoved for all streams/endpoints in conference
export function sendCleanupEvents(): Action {
    return new class {
        run(event: Event): void {
            let session = event.data().session as ConferenceSessionImpl;
            let streams: StreamImpl[] = extractStreamInfo(session.conferenceInfo);

            streams.forEach(stream => {
                debugEvent('streamRemoved event', stream);

                let streamToSend = session.streams[stream.id];
                if (!streamToSend) {
                    streamToSend = stream;
                }

                if (!!session.eventHandlers.streamRemoved) {
                    session.eventHandlers.streamRemoved(streamToSend);
                }
            });

            let endpoints: Endpoint[] = extractEndpointInfo(session.conferenceInfo);

            endpoints.forEach(endpoint => {
                if (endpoint.endpoint == session.stateContainer.endpoint) {
                    // skip this
                    return;
                }

                debugEvent('endpointRemoved event', endpoint);

                if (!!session.eventHandlers.endpointLeft) {
                    session.eventHandlers.endpointLeft(endpoint);
                }
            })
        }
    }
}

/** Private Functions */
function updateConferenceInfo(conferenceInfoXml: string, session: ConferenceSessionImpl): void {
    xml2js.parseString(conferenceInfoXml, (err, result) => {
        let updateConferenceInfo = false
        let oldConferenceInfo: Object = session.conferenceInfo

        if (!session.conferenceInfo) {
            updateConferenceInfo = true;
        } else {
            let oldVersion: number = Number(session.conferenceInfo['$']['version']);
            let newVersion: number = Number(result['conference-info']['$']['version']);

            if (newVersion > oldVersion) {
                updateConferenceInfo = true;
            }
        }

        if (updateConferenceInfo) {
            session.conferenceInfo = result['conference-info'];
        }

        emitParticipantEvents(oldConferenceInfo, session);
    })
}

function emitParticipantEvents(oldConferenceInfo: Object, session: ConferenceSessionImpl): void {
    let newConferenceInfo = session.conferenceInfo;

    let oldEndpoints: Endpoint[] = extractEndpointInfo(oldConferenceInfo);
    let newEndpoints: Endpoint[] = extractEndpointInfo(newConferenceInfo);
    let removedEndpoints: Endpoint[] = endpointsDiff(oldEndpoints, newEndpoints);
    let addedEndpoints: Endpoint[] = endpointsDiff(newEndpoints, oldEndpoints);

    let oldStreams: StreamImpl[] = extractStreamInfo(oldConferenceInfo)
    let newStreams: StreamImpl[] = extractStreamInfo(newConferenceInfo)
    let removedStreams: StreamImpl[] = streamsDiff(oldStreams, newStreams)
    let addedStreams: StreamImpl[] = streamsDiff(newStreams, oldStreams)

    removedStreams.forEach((stream) => {
        session.fsm.handle(new ConferenceXmlStreamEvent('removed',
            !!session.streams[stream.id] ? session.streams[stream.id] : stream,
            session));
    })

    removedEndpoints.forEach((endpoint) => {
        session.fsm.handle(new ConferenceXmlEndpointEvent('removed', endpoint, session));
    });

    addedEndpoints.forEach((endpoint) => {
        session.fsm.handle(new ConferenceXmlEndpointEvent('added', endpoint, session));
    });

    addedStreams.forEach((stream) => {
        session.fsm.handle(new ConferenceXmlStreamEvent('added', stream, session));
    })
}

function extractStreamInfo(conferenceInfo: Object): StreamImpl[] {
    let retval: StreamImpl[] = []

    if (!!conferenceInfo && conferenceInfo['users'][0]['user']) {
        let conferenceUri: string = conferenceInfo['$']['entity']
        let conferenceName: string = conferenceUri.match('^sip:(.*)@.*$')[1]

        conferenceInfo['users'][0]['user'].forEach(user => {
            if (!!user['endpoint']) {
                user['endpoint'].forEach(endpoint => {
                    if (!!endpoint['media']) {
                        endpoint['media'].forEach(media => {
                            let id: string = media.$['id']
                            let srcId: string = media['src-id'][0]
                            let type: string = media['type'][0]
                            let sipUrl: string = user.$['entity']
                            let username: string = sipUrl.match('^sip:(.*)@.*$')[1]

                            let originator: Endpoint = { username: username, endpoint: endpoint["$"]["entity"] }

                            let stream: StreamImpl = new StreamImpl(id, type.toLowerCase(), originator);
                            stream.sipUrl = srcId;
                            retval.push(stream);
                        })
                    }
                })
            }
        })
    }

    return retval;
}

function streamsDiff(a: Array<StreamImpl>, b: Array<StreamImpl>): Array<StreamImpl> {
    let retval: Array<StreamImpl> = []

    a.forEach(aVal => {
        let insert = true;

        b.forEach(bVal => {
            if (bVal.id == aVal.id) {
                insert = false
            }
        });

        if (insert) {
            retval.push(aVal)
        }
    })

    return retval;
}

function extractEndpointInfo(conferenceInfo: Object): Endpoint[] {
    let retval: Endpoint[] = []

    if (!!conferenceInfo && conferenceInfo['users'][0]['user']) {
        let conferenceUri: string = conferenceInfo['$']['entity']
        let conferenceName: string = conferenceUri.match('^sip:(.*)@.*$')[1]

        conferenceInfo['users'][0]['user'].forEach(user => {
            if (!!user['endpoint']) {
                user['endpoint'].forEach(endpoint => {
                    let sipUrl: string = user.$['entity']
                    let username: string = sipUrl.match('^sip:(.*)@.*$')[1]

                    retval.push({ username: username, endpoint: endpoint['$']['entity'] });
                })
            }
        })
    }

    return retval
}

function endpointsDiff(a: Array<Endpoint>, b: Array<Endpoint>): Array<Endpoint> {
    let retval: Array<Endpoint> = []

    a.forEach(aVal => {
        let insert = true;

        b.forEach(bVal => {
            if (bVal.endpoint == aVal.endpoint) {
                insert = false
            }
        });

        if (insert) {
            retval.push(aVal)
        }
    })

    return retval;
}

function subscribe(session: ConferenceSessionImpl, expires?: number, eventHandlers?: Object) {
    let uri = session.session.remote_identity.uri.toString()

    if (expires == undefined) {
        expires = 3600; // default
    }

    if (expires > 0) {
        debug(`will now subscribe to ${uri}`);
    } else if (expires == 0) {
        debug(`will unsubscribe from ${uri}`);
    } else {
        throw new Error(`invalid expires parameter ${expires}`);
    }

    let extraHeaders = [
        'Event: conference',
        'Accept: application/conference-info+xml',
        `Expires: ${expires}`
    ]

    let options = {
        extraHeaders: extraHeaders,
        eventHandlers: eventHandlers
    }

    setTimeout(() => session.subscription = session.stateContainer.phone.subscribe(uri, options), 500);
}

function cleanupSessionStreams(session: ConferenceSessionImpl) {
    Object.keys(session.streams).forEach(id => {
        session.streams[id].terminate();
        delete session.streams[id];
    });
}

function terminateSession(session: ConferenceSessionImpl, options?: any) {
    // end conference session
    setTimeout(() => {
        session.session.terminate(options);
        session = null;
    }, 1000);
}

function exitConference(session: ConferenceSessionImpl, end: boolean) {
    debug('SipService.leaveConference()');

    session.subscription.unsubscribe();

    cleanupSessionStreams(session);

    let extraHeaders = [];

    if (end) {
        extraHeaders.push('X-Conference-End: true');
    }

    let options = {
        extraHeaders: extraHeaders
    }

    terminateSession(session, options);
}

function publishStreamToRouterConference(session: ConferenceSessionImpl, stream: StreamImpl
    , resolve: Function, reject: Function) {

    let extraHeaders = [
        `${STREAM_ID_HEADER}: ${stream.id}`,
        `${STREAM_TYPE_HEADER}: ${stream.type}`,
        `${ICE_EXCHANGE_HEADER}: async`
    ]

    let eventHandlers = [];
    JSSIP_SESSION_EVENTS.forEach(eventId => eventHandlers[eventId] =
        function (eventData: any) {
            if (!stream.session) {
                stream.session = this;
            }
            session.fsm.handle(new PublishStreamSessionEvent(eventId, eventData, session, stream, resolve, reject));
        });

    let rtcOfferConstraints = getCrossBrowserRtcOfferConstraints({
        offerToReceiveAudio: 0,
        offerToReceiveVideo: 0
    });

    let options = {
        eventHandlers: eventHandlers,
        extraHeaders: extraHeaders,
        mediaStream: stream.mediaStream,
        rtcOfferConstraints: rtcOfferConstraints,
        pcConfig: {
            iceServers: ICE_SERVERS
        }
    }

    let conferenceUri = conferenceSipURI(session.conference.name);

    session.stateContainer.phone.call(conferenceUri, options)
}

function publishStreamToP2PConference(session: ConferenceSessionImpl, stream: StreamImpl, resolve: Function, reject: Function) {

    let extraHeaders = [
        `${STREAM_ID_HEADER}: ${stream.id}`,
        `${STREAM_TYPE_HEADER}: ${stream.type}`
    ]

    let eventHandlers = [];
    JSSIP_SESSION_EVENTS.forEach(eventId => eventHandlers[eventId] =
        function (eventData: any) {
            if (!stream.session) {
                stream.session = this;
            }
            session.fsm.handle(new PublishStreamSessionEvent(eventId, eventData, session, stream, resolve, reject));
        });

    let rtcOfferConstraints = getCrossBrowserRtcOfferConstraints({
        offerToReceiveAudio: 0,
        offerToReceiveVideo: 0
    });

    let options = {
        eventHandlers: eventHandlers,
        extraHeaders: extraHeaders,
        mediaConstraints: { 'audio': false, 'video': false },
        rtcOfferConstraints: rtcOfferConstraints,
        send_empty_sdp: true
    }

    let conferenceUri = conferenceSipURI(session.conference.name);

    session.stateContainer.phone.call(conferenceUri, options)
}

function getConferenceSession(stateContainer: AuviousStateContainer, streamId: string): ConferenceSessionImpl {
    let keys = Object.keys(stateContainer.conferenceSessions);

    for (let i = 0; i < keys.length; i++) {
        let key = keys[i];
        let conferenceSession = stateContainer.conferenceSessions[key];

        if (!!conferenceSession.streams[streamId]) {
            return conferenceSession;
        }
    }

    return null;
}


