import {
    Conference, ConferenceSession, ConferenceSessionEventHandlers,
    Stream, StreamType, PublishOptions, Endpoint
} from './AuviousApi';
import { ConferenceSessionImplFsm } from './ConferenceSessionImplFsm';
import { AuviousStateContainer } from './AuviousStateContainer';
import { StreamImpl } from './StreamImpl';
import { debug } from './Debug';
import { uuid } from './Common';
import { Promise } from 'es6-promise';

export class ConferenceSessionImpl implements ConferenceSession {
    id: string;

    session: any; //jssip Session
    subscription: any; // jssip Subscription
    stateContainer: AuviousStateContainer;
    eventHandlers: ConferenceSessionEventHandlers
    notifications: number = 0;
    conference: Conference;
    conferenceInfo: Object;
    streams: Object; // <stream id, stream obj>
    remoteEndpointData: Endpoint;

    fsm: ConferenceSessionImplFsm;

    constructor(session: any, stateContainer: AuviousStateContainer, eventHandlers?: ConferenceSessionEventHandlers) {
        this.id = uuid();
        this.session = session;

        if (!!eventHandlers) {
            this.eventHandlers = eventHandlers;
        } else {
            this.eventHandlers = {};
        }

        if (!!session) {
            this.remoteEndpointData = { username: session.remote_identity.uri.user, endpoint: null };
        }

        this.streams = {};
        this.stateContainer = stateContainer;

        this.fsm = new ConferenceSessionImplFsm(this);
    }

    get direction() {
        return this.fsm.direction;
    }

    get remoteEndpoint() {
        return this.fsm.remoteEndpoint;
    }

    isInProgress(): boolean {
        return this.fsm.isInProgress();
    }

    isEstablished(): boolean {
        return this.fsm.isEstablished();
    }

    publish(options: PublishOptions): Promise<Stream> {
        return this.fsm.publish(options);
    }

    unpublish(stream: Stream): void {
        this.fsm.unpublish(stream);
    }

    leave(): void {
        this.fsm.leave();
    }

    terminate(): void {
        this.fsm.terminate();
    }

    on(eventId: string, handler: Function) {
        this.fsm.on(eventId, handler);
    }
}


