export const STREAM_TYPE_HEADER: string = "X-Stream-Type";
export const CONFERENCE_MODE_HEADER: string = "X-Conference-Mode";
export const CONFERENCE_ID_HEADER: string = "X-Conference-Id";
export const P2P_STREAM_ID_HEADER: string = "X-P2p-Stream-Id";
export const ICE_EXCHANGE_HEADER: string = "X-Ice-Exchange";
export const DOMAIN: string = "auvious.com";
export const CONFERENCE_DOMAIN: string = "conference.auvious.com";
export const STREAM_DOMAIN: string = "stream.auvious.com";
export const CONFERENCE_FACTORY: string = "conference.factory";
export const SESSION_ID_HEADER: string = "X-Session-Id";
export const AUVIOUS_CONFERENCE_CLIENT_VERSION: string = "1.0";
export const AUVIOUS_CONFERENCE_CLIENT_HEADER: string = "X-Auvious-Conference-Client";

import { Promise } from 'es6-promise';

// Opera 8.0+
var isOpera = (!!window.opr && !!opr.addons) || !!window.opera || navigator.userAgent.indexOf(' OPR/') >= 0;

// Firefox 1.0+
var isFirefox = typeof InstallTrigger !== 'undefined';

// Safari 3.0+ "[object HTMLElementConstructor]" 
var isSafari = /constructor/i.test(window.HTMLElement) || (function (p) { return p.toString() === "[object SafariRemoteNotification]"; })(!window['safari'] || safari.pushNotification);

// Internet Explorer 6-11
var isIE = /*@cc_on!@*/false || !!document.documentMode;

// Edge 20+
var isEdge = !isIE && !!window.StyleMedia;

// Chrome 1+
var isChrome = !!window.chrome && !!window.chrome.webstore;

// Blink engine detection
var isBlink = (isChrome || isOpera) && !!window.CSS;

export function sipURI(username: string) {
    return "sip:" + username + "@" + DOMAIN;
}

export function conferenceSipURI(conferenceId: string) {
    return "sip:" + conferenceId + "@" + CONFERENCE_DOMAIN;
}

export function conferenceFactorySipURI() {
    return sipURI(CONFERENCE_FACTORY);
}

export function uriToEndpoint(uri: any) {
    let transport = "udp";

    if (!!uri.parameters.transport) {
        transport = uri.parameters.transport;
    }

    return `${uri.scheme}:${uri.user}@${uri.host}:${uri.port};${transport}`;
}

export function getCrossBrowserRtcOfferConstraints(rtcOfferOptions: RTCOfferOptions) {
    let oldSpecRTCOfferOptions = false;

    // new spec same as argument
    if (!oldSpecRTCOfferOptions) {
        return rtcOfferOptions;
    }

    // old spec
    return {
        mandatory: {
            OfferToReceiveAudio: rtcOfferOptions.offerToReceiveAudio == 1 ? true : false,
            OfferToReceiveVideo: rtcOfferOptions.offerToReceiveVideo == 1 ? true : false
        }
    }
}

export function getCrossBrowserRtcAnswerConstraints(rtcAnswerOptions: RTCOfferOptions) {
    return getCrossBrowserRtcOfferConstraints(rtcAnswerOptions);
}

import _xml2js = require('xml2js');
import _uuid = require('uuid');

export const xml2js = _xml2js;
export const uuid = _uuid;

declare var isUsingTemasysPlugin, attachMediaStream;

export function isUsingTemasysPlugin2(): boolean {
    return (typeof isUsingTemasysPlugin !== 'undefined') && isUsingTemasysPlugin;
}

export function getTemasysGetUserMedia(): Function {
    let getUserMedia = function(constraints) {
        return navigator.mediaDevices.getUserMedia(constraints);
    }

    if (isUsingTemasysPlugin2()) {
        getUserMedia = function (constraints) {
            return new Promise((resolve, reject) => {
                navigator.getUserMedia(constraints, function (stream) {
                    resolve(stream);
                }, function (error) {
                    reject(error);
                });
            });
        };
    }

    return getUserMedia;
}



