import { AuviousState, State } from './AuviousState';
import { UAEventHandlers, Session, ConferenceSession } from './AuviousApi';
import { DisconnectedState } from './states/DisconnectedState';
import { DisconnectingState } from './states/DisconnectingState';
import { ConnectingState } from './states/ConnectingState';
import { ConnectedState } from './states/ConnectedState';
import { RegisteringState } from './states/RegisteringState';
import { RegisteredState } from './states/RegisteredState';
import { UnregisteringState } from './states/UnregisteringState';
import { UnregisteredState } from './states/UnregisteredState';
import { debug } from './Debug';
import { Event } from './Event';
import { EventHandler } from './EventHandler';
import { ConferenceSessionImpl } from "./ConferenceSessionImpl";
import { SessionImpl } from "./SessionImpl";

export class AuviousStateContainer implements EventHandler {
    focusUrl: string = "ws://localhost:5082";

    state: AuviousState; // current state
    states: AuviousState[];

    identity: string;
    endpoint: string;
    phone: any;
    eventHandlers: UAEventHandlers;

    // call sessions
    sessions: SessionImpl[];
    conferenceSessions: ConferenceSessionImpl[];

    constructor() {
        this.states = [
            new DisconnectedState(this),
            new ConnectingState(this),
            new ConnectedState(this),
            new DisconnectingState(this),
            new RegisteringState(this),
            new RegisteredState(this),
            new UnregisteringState(this),
            new UnregisteredState(this)
        ];

        this.state = this.getState(State.DISCONNECTED);
        this.eventHandlers = {};
        this.sessions = [];
        this.conferenceSessions = [];
    }

    getState(state: string): AuviousState {
        let retval: AuviousState;

        for (let i = 0; i < this.states.length; i++) {
            if (this.states[i].state == state) {
                retval = this.states[i];
                break;
            }
        }

        return retval;
    }

    switchState(state: string): void {
        let newState = this.getState(state);

        if (!newState) {
            throw new Error(`${state} state not found`);
        }

        this.state = newState;
    }

    // EventHandler
    handle(event: Event): void {
        this.state.rules.forEach(rule => rule.process(event));
    }

}


