import { Action } from './Action';
import { Event, ApiEvent, UAEvent, SessionEvent } from './Event';
import { State, AuviousState } from './AuviousState';
import { AuviousStateContainer } from './AuviousStateContainer';
import { CallOptions, Session, Stream, RegisterOptions, ConnectOptions, Message } from './AuviousApi';
import { SessionImpl } from './SessionImpl';
import { debug, debugEvent } from './Debug';
import { JSSIP_UA_EVENTS, JSSIP_SESSION_EVENTS, ICE_SERVERS, JsSIP } from './JSSIP';
import {
    sipURI, SESSION_ID_HEADER, P2P_STREAM_ID_HEADER, ICE_EXCHANGE_HEADER,
    getCrossBrowserRtcOfferConstraints
} from './Common';
import { ConnectingState } from './states/ConnectingState';
import { RegisteringState } from './states/RegisteringState';
import { UnregisteringState } from './states/UnregisteringState';
import { DisconnectingState } from './states/DisconnectingState';

export function register(): Action {
    return new class {
        run(event: Event): void {
            let options = event.data().options as RegisterOptions;
            let stateContainer = event.data().stateContainer as AuviousStateContainer;

            if (!!options.focusUrl) {
                stateContainer.focusUrl = options.focusUrl;
            }

            let socket = new JsSIP.WebSocketInterface(stateContainer.focusUrl);

            socket.via_transport = 'ws';

            let configuration = {
                sockets: socket,
                uri: sipURI(options.username),
                register: true,
                use_info_for_ice: true
            };

            if (!!options.password) {
                configuration['password'] = options.password;
            }

            let identity = options.username + "(not registered)";
            let phone: any = new JsSIP.UA(configuration);

            stateContainer.identity = identity;
            stateContainer.phone = phone;
            if (!!options.eventHandlers) {
                stateContainer.eventHandlers = options.eventHandlers;
            }

            phone.registrator().setBody(options.body);
            phone.registrator().setContentType(options.contentType);

            JSSIP_UA_EVENTS.forEach(event => phone.on(event, (data) => stateContainer.handle(new UAEvent(event, data, stateContainer))));

            phone.start();
        }
    }
}

export function connect(): Action {
    return new class {
        run(event: Event): void {
            let options = event.data().options as ConnectOptions;
            let stateContainer = event.data().stateContainer as AuviousStateContainer;

            if (!!options.focusUrl) {
                stateContainer.focusUrl = options.focusUrl;
            }

            let socket = new JsSIP.WebSocketInterface(stateContainer.focusUrl);

            socket.via_transport = 'ws';

            let configuration = {
                sockets: socket,
                uri: sipURI(options.username),
                register: false,
                use_info_for_ice: true
            };


            let identity = options.username + "(not registered)";
            let phone: any = new JsSIP.UA(configuration);

            stateContainer.identity = identity;
            stateContainer.phone = phone;
            if (!!options.eventHandlers) {
                stateContainer.eventHandlers = options.eventHandlers;
            }

            JSSIP_UA_EVENTS.forEach(event => phone.on(event, (data) => stateContainer.handle(new UAEvent(event, data, stateContainer))));

            phone.start();
        }
    }
}

export function call(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof ApiEvent)) {
                throw new Error("Invalid Event");
            }

            let stateContainer = event.data().stateContainer as AuviousStateContainer;
            let username = event.data().username;
            let options = event.data().options as CallOptions;

            let phone = stateContainer.phone;

            let callOptions = {
                pcConfig: {
                    iceServers: ICE_SERVERS
                }
            };

            if (!!options && !!options.mediaConstraints) {
                let audio = true;
                let video = true;

                if (options.mediaConstraints.hasOwnProperty('audio')) {
                    audio = options.mediaConstraints.audio;
                }

                if (options.mediaConstraints.hasOwnProperty('audio')) {
                    video = options.mediaConstraints.video;
                }

                callOptions['mediaConstraints'] = {
                    audio: audio,
                    video: video
                };
            }

            if (!!options && !!options.receiveMediaConstraints) {
                let audio = true;
                let video = true;

                if (options.receiveMediaConstraints.hasOwnProperty('audio')) {
                    audio = options.receiveMediaConstraints.audio;
                }

                if (options.receiveMediaConstraints.hasOwnProperty('video')) {
                    video = options.receiveMediaConstraints.video;
                }

                let rtcOfferConstraints = getCrossBrowserRtcOfferConstraints({
                    offerToReceiveAudio: audio ? 1 : 0,
                    offerToReceiveVideo: video ? 1 : 0
                });

                callOptions['rtcOfferConstraints'] = rtcOfferConstraints;
            }

            if (!!options && !!options.mediaStream) {
                callOptions['mediaStream'] = options.mediaStream;
            }

            let session: SessionImpl = new SessionImpl(null, options.eventHandlers);
            session.remoteEndpointData = { username: username, endpoint: null };
            stateContainer.sessions[session.id] = session;
            callOptions['extraHeaders'] = [
                `${SESSION_ID_HEADER}: ${session.id}`,
                `${ICE_EXCHANGE_HEADER}: async`
            ];

            debug('call options: ', callOptions);
            session.session = phone.call(username, callOptions);
        }
    }
}

export function message(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof ApiEvent)) {
                throw new Error("Invalid Event");
            }

            let stateContainer = event.data().stateContainer as AuviousStateContainer;
            let username = event.data().username;
            let message = event.data().message;
            let resolve = event.data().resolve;
            let reject = event.data().reject;
            let phone = stateContainer.phone;

            let eventHandlers = {
                succeeded: function (data) {
                    debug('message succeeded', data);
                    resolve();
                },
                failed: function (data) {
                    debug('message failed', data);
                }
            };

            let options = {
                eventHandlers: eventHandlers
            };

            phone.sendMessage(username, message, options);
        }
    }
}

export function print(message?: string): Action {
    return new class {
        run(event: Event): void {
            if (!!message) {
                debug(message, event);
            } else {
                debug(`received event of type: ${event.type()}`, event.data());
            }
        }
    };
}

export function switchState(state: string): Action {
    return new class {
        run(event: Event): void {
            let stateContainer: AuviousStateContainer = event.data().stateContainer;

            if (!stateContainer) {
                throw new Error("stateContainer not found in event: " + event.type());
            }

            stateContainer.switchState(state);
        }
    }
}

export function updateIdentity(): Action {
    return new class {
        run(event: Event): void {
            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            let data: any = event.data().eventData;

            stateContainer.identity = data.response.from.uri.user;
        }
    }
}

export function updateEndpoint(): Action {
    return new class {
        run(event: Event): void {
            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            let data: any = event.data().eventData;

            data.response.headers['Contact'].forEach(contact => {
                if (stateContainer.phone.contact.uri.user == contact.parsed.uri.user) {
                    let endpoint = contact.parsed.uri.toString();

                    if (!endpoint.match(/;transport=ws/)) {
                        endpoint = endpoint + ";transport=ws";
                    }
                    stateContainer.endpoint = endpoint;
                }
            });
        }
    }
}

export function sendConnectedEvent(): Action {
    return new class {
        run(event: Event): void {
            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            if (!!stateContainer.eventHandlers.connected) {
                stateContainer.eventHandlers.connected({});
            }
        }
    }
}

export function sendConnectingEvent(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof UAEvent)) {
                throw new Error("Invalid Event");
            }

            let stateContainer: AuviousStateContainer = event.data().stateContainer;

            debugEvent('connecting event');

            if (!!stateContainer.eventHandlers.connecting) {
                stateContainer.eventHandlers.connecting({ attempts: event.data().eventData.attempts });
            }
        }
    }
}

