export function sendNewSessionEvent(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof UAEvent) && !(event instanceof ApiEvent)) {
                throw new Error("Invalid Event");
            }

            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            let session: Session = event.extra.session;

            if (!session) {
                throw new Error("session not found!");
            }

            debugEvent('newSession event', session);

            if (!!stateContainer.eventHandlers.newSession) {
                stateContainer.eventHandlers.newSession(session);
            }
        }
    }
}

export function sendRegistrationFailedEvent(customMessage?: string): Action {
    return new class {
        run(event: Event): void {
            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            if (!!stateContainer.eventHandlers.registrationFailed) {
                let message = !!customMessage ? customMessage : event.data().eventData.cause;
                let error: Error = new Error(message);
                if (!error.message || error.message == "") {
                    error.message = "unknown error";
                }
                stateContainer.eventHandlers.registrationFailed(error);
            }
        }
    }
}

export function sendNewMessageEvent(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof UAEvent)) {
                throw new Error("invalid event");
            }

            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            if (!stateContainer.eventHandlers.newMessage) {
                return;
            }

            let sender = event.eventData.message.remote_identity.uri.user;
            let recipient = event.eventData.message.local_identity.uri.user;
            let contentType = event.eventData.message.content_type;
            let content = event.eventData.message.content;

            let message: Message = {
                sender: sender,
                recipient: recipient,
                contentType: contentType,
                content: content,
            };

            stateContainer.eventHandlers.newMessage(message);
        }
    }
}

