export function setupHandler(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof ApiEvent)) {
                throw new Error("Invalid Event");
            }

            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            stateContainer.eventHandlers[event.data().event] = event.data().handler;
        }
    }
}

export function setupNewRTCSession(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof UAEvent)) {
                throw new Error("Invalid Event");
            }

            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            let jssipSession: any = event.data().eventData.session;
            let eventSession: SessionImpl;

            if (event.eventData.originator == 'local') {
                let id = getExtraHeaderValue(event.eventData.request.extraHeaders, SESSION_ID_HEADER);
                eventSession = stateContainer.sessions[id];
                eventSession.session = jssipSession;
            } else {
                eventSession = new SessionImpl(jssipSession);
                stateContainer.sessions[eventSession.id] = eventSession;
            }

            event.extra.session = eventSession;

            JSSIP_SESSION_EVENTS.forEach(eventId => jssipSession.on(eventId,
                function (eventData: any) {
                    eventSession.fsm.handle(new SessionEvent(eventId, eventData, eventSession, stateContainer));
                }));
        }
    }
}

export function stopPhone(): Action {
    return new class {
        run(event: Event): void {
            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            if (!!stateContainer.phone) {
                stateContainer.phone.stop();
                stateContainer.phone = null;
            }
        }
    }
}

