export function forwardConferenceP2PStreamSessionToConferenceSession(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof UAEvent)) {
                throw new Error(`Invalid event ${event.type()}`)
            }

            let data = event.eventData;
            let stateContainer = event.stateContainer;

            let streamId = data.request.headers[P2P_STREAM_ID_HEADER][0].raw;

            let conferenceSession;

            let keys = Object.keys(stateContainer.conferenceSessions);

            for (let i = 0; i < keys.length; i++) {
                if (!!stateContainer.conferenceSessions[keys[i]].streams[streamId]) {
                    conferenceSession = stateContainer.conferenceSessions[keys[i]];
                }
            }

            if (!conferenceSession) {
                throw new Error("conference session with stream " + streamId + " not found!");
            }

            conferenceSession.fsm.handle(event);
        }
    }
}

export function setupConnectingPromise(): Action {
    return new class {
        run(event: Event): void {
            let resolve = event.data().resolve as Function;
            let reject = event.data().reject as Function;
            let stateContainer = event.data().stateContainer as AuviousStateContainer;

            let state = stateContainer.getState(State.CONNECTING) as ConnectingState;
            state.resolve = resolve;
            state.reject = reject;
        }
    }
}

