// utility functions
function getExtraHeaderValue(extraHeaders: string[], header: string): string {
    let retval = null;

    let regexp = new RegExp('^(.*): (.*)$');
    for (let i = 0; i < extraHeaders.length; i++) {
        let value = extraHeaders[i];
        let result: RegExpExecArray = regexp.exec(value);
        //debug("result", result);
        if (result.length == 3 && result[1] == header) {
            retval = result[2];
            break;
        }
    }

    return retval;
}


