import { debug as factory } from 'debug';

import { isEdge } from './Browser';

if (isEdge) {
    factory.useColors = () => false;
}

export var debug = factory('auvious:api');
export var debugEvent = factory('auvious:event');
export var debugError = factory('auvious:error');

debugError.log = console.error.bind(console);




