import { BaseConferenceSessionState } from "./BaseConferenceSessionState";
import { ConferenceSessionState } from "../../AuviousState";
import { debug } from "../../Debug";
import { PublishOptions, Stream } from "../../AuviousApi";
import { ApiEvent } from "../../Event";

import { Promise } from 'es6-promise';
import { joinConferenceSessionEvent, notifiedForTheFirstTime, notified, publishStreamSessionEvent, viewStreamSessionEvent, p2pStreamSessionEvent, conferenceXmlEndpointEvent, conferenceXmlStreamEvent, conferenceXmlEndpointEventIsNotMe, conferenceXmlStreamEventIsNotMe } from "../../ConferenceConditions";
import {
    switchState, sendJoinConferenceEndedEvent, sendJoinConferenceFailedEvent,
    publishStream, unpublishStream, leaveConference, endConference,
    resolveJoinConferencePromise, increaseSessionNotifications, generateConferenceEvents,
    sendPublishStreamAddedEvent, resolvePublishStreamPromise, rejectPublishStreamPromise,
    removeFromSessionStreams, addToSessionStreams, connectToStream, sendViewStreamAddedEvent,
    sendStreamRemovedEvent, sendEndpointJoinedEvent, sendEndpointLeftEvent,
    disconnectFromStream, answerP2PStreamSession, removeP2PStreamSession,
    removeFromConferenceSessions, disconnectStreams, sendCleanupEvents,
    setupOnAddStream
} from "../../ConferenceActions";
import { apiRequest, newIncomingConferenceP2PStreamSession } from "../../Conditions";

export class EstablishedConferenceSessionState extends BaseConferenceSessionState {
    state = ConferenceSessionState.ESTABLISHED;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('established conference session state rule setup');

        // Api Events
        when(apiRequest('publishStream'))
            .then(publishStream())
            .and(addToSessionStreams());

        when(apiRequest('unpublishStream'))
            .then(unpublishStream());

        when(apiRequest('leaveConference'))
            .then(switchState(ConferenceSessionState.TERMINATING))
            .and(sendCleanupEvents())
            .and(leaveConference());

        when(apiRequest('endConference'))
            .then(switchState(ConferenceSessionState.TERMINATING))
            .and(sendCleanupEvents())
            .and(endConference());

        // Jssip phone events
        when(newIncomingConferenceP2PStreamSession())
            .then(answerP2PStreamSession())

        // Join Conference Session Events
        when(notifiedForTheFirstTime())
            .then(resolveJoinConferencePromise())

        when(notified())
            .then(increaseSessionNotifications())
            .and(generateConferenceEvents())

        when(joinConferenceSessionEvent('ended'))
            .then(switchState(ConferenceSessionState.TERMINATING))
            .and(sendCleanupEvents())
            .and(disconnectStreams())
            .and(removeFromConferenceSessions())
            .and(switchState(ConferenceSessionState.TERMINATED))
            .and(sendJoinConferenceEndedEvent())

        when(joinConferenceSessionEvent('failed'))
            .then(switchState(ConferenceSessionState.FAILED))
            .and(removeFromConferenceSessions())
            .and(sendJoinConferenceFailedEvent())

        // jssip publish session events
        when(publishStreamSessionEvent('connecting'))
            .then(sendPublishStreamAddedEvent())
        when(publishStreamSessionEvent('confirmed'))
            .then(resolvePublishStreamPromise())
        when(publishStreamSessionEvent('failed'))
            .then(rejectPublishStreamPromise())
            .and(removeFromSessionStreams())
            .and(sendStreamRemovedEvent())
        when(publishStreamSessionEvent('ended'))
            .then(removeFromSessionStreams())
            .and(sendStreamRemovedEvent())

        // jssip view session events
        when(viewStreamSessionEvent('peerconnection'))
            .then(setupOnAddStream());
        when(viewStreamSessionEvent('addstream'))
            .then(sendViewStreamAddedEvent());
        when(viewStreamSessionEvent('ended'))
            .then(removeFromSessionStreams())
            .and(sendStreamRemovedEvent());
        when(viewStreamSessionEvent('failed'))
            .then(removeFromSessionStreams());

        // jssip p2p stream session events
        when(p2pStreamSessionEvent('ended').or(p2pStreamSessionEvent('failed')))
            .then(removeP2PStreamSession())

        // conference xml processing events
        when(conferenceXmlEndpointEvent('added').and(conferenceXmlEndpointEventIsNotMe()))
            .then(sendEndpointJoinedEvent());
        when(conferenceXmlEndpointEvent('removed').and(conferenceXmlEndpointEventIsNotMe()))
            .then(sendEndpointLeftEvent());
        when(conferenceXmlStreamEvent('added').and(conferenceXmlStreamEventIsNotMe()))
            .then(addToSessionStreams())
            .and(connectToStream());
        when(conferenceXmlStreamEvent('removed').and(conferenceXmlStreamEventIsNotMe()))
            .then(disconnectFromStream());
    }

    publish(options: PublishOptions): Promise<Stream> {
        return new Promise<Stream>((resolve, reject) => {
            let event = new ApiEvent('publishStream', {
                session: this.fsm.conferenceSession,
                options: options,
                resolve: resolve,
                reject: reject
            });
            this.fsm.handle(event);
        });
    }

    unpublish(stream: Stream): void {
        let event = new ApiEvent('unpublishStream', {
            session: this.fsm.conferenceSession,
            stream: stream
        });
        this.fsm.handle(event);
    }

    leave(): void {
        let event = new ApiEvent('leaveConference', {
            session: this.fsm.conferenceSession
        });
        this.fsm.handle(event);
    }

    terminate(): void {
        let event = new ApiEvent('endConference', {
            session: this.fsm.conferenceSession
        });
        this.fsm.handle(event);
    }
}


