import { Event } from './Event';

export interface Matcher {
    matches(event: Event): boolean;
}

export class Condition {
    private matcher: Matcher;
    private andConditions: Condition[];
    private orConditions: Condition[];

    constructor(matcher: Matcher) {
        this.matcher = matcher;
        this.andConditions = [];
        this.orConditions = [];
    }

    matches(event: Event): boolean {
        let retval = this.matcher.matches(event);

        if (retval) {
            for (let i = 0; retval && i < this.andConditions.length; i++) {
                retval = this.andConditions[i].matches(event);
            }
        }

        if (!retval) {
            for (let i = 0; !retval && i < this.orConditions.length; i++) {
                retval = this.orConditions[i].matches(event);
            }
        }

        return retval;
    }

    and(c: Condition): Condition {
        this.andConditions.push(c);
        return this;
    }

    or(c: Condition): Condition {
        this.orConditions.push(c);
        return this;
    }
}


