import { SessionState } from '../../AuviousState';

import { debug } from '../../Debug';
import { switchState, sendFailedEvent, sendRemoteStreamAddedEvent, sendEndedEvent } from "../../SessionActions";
import { sessionEvent } from "../../Conditions";
import { BaseSessionState } from "./BaseSessionState";
import { ICE_EXCHANGE_HEADER } from "../../Common";

import { Promise } from 'es6-promise';

export class EstablishedSessionState extends BaseSessionState {
    readonly state = SessionState.ESTABLISHED;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('established session state rule setup');

        when(sessionEvent('ended'))
            .then(switchState(SessionState.TERMINATED))
            .and(sendEndedEvent())

        when(sessionEvent('failed'))
            .then(switchState(SessionState.FAILED))
            .and(sendFailedEvent())

        when(sessionEvent('addstream'))
            .then(sendRemoteStreamAddedEvent());
    }

    hold(): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            try {
                this.fsm.session.session.hold({
                    extraHeaders: [ `${ICE_EXCHANGE_HEADER}: async`]
                }, resolve);
            } catch (error) {
                reject(new Error(error));
            }
        });
    }

    unhold(): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            try {
                this.fsm.session.session.unhold({
                    extraHeaders: [ `${ICE_EXCHANGE_HEADER}: async`]
                }, resolve);
            } catch (error) {
                reject(new Error(error));
            }
        });
    }

    terminate() {
        this.fsm.session.session.terminate();
    }
}


