import { State } from '../AuviousState';
import { uaEvent } from '../Conditions';
import { switchState, sendDisconnectedEvent } from '../Actions';

import { debug } from '../Debug';
import { BaseAuviousState } from "./BaseAuviousState";

export class UnregisteredState extends BaseAuviousState {
    readonly state = State.UNREGISTERED;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('unregistered state rule setup');

        when(uaEvent('disconnected'))
            .then(switchState(State.DISCONNECTED))
            .and(sendDisconnectedEvent());
    }

}


