import { SessionState } from '../../AuviousState';

import { debug } from '../../Debug';
import {
    switchState, sendFailedEvent, sendLocalStreamAddedEvent, setupOnAddStream,
    sendAcceptedEvent, sendRemoteStreamAddedEvent, updateSessionEndpointData
} from "../../SessionActions";
import { sessionEvent, sessionOriginator } from "../../Conditions";
import { BaseSessionState } from "./BaseSessionState";
import { AnswerOptions } from "../../AuviousApi";
import { ICE_SERVERS } from "../../JSSIP";
import { ICE_EXCHANGE_HEADER, getCrossBrowserRtcAnswerConstraints } from "../../Common";

export class InProgressSessionState extends BaseSessionState {
    readonly state = SessionState.IN_PROGRESS;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('in progress session state rule setup');

        when(sessionEvent('peerconnection'))
            .then(setupOnAddStream());

        when(sessionEvent('connecting'))
            .then(sendLocalStreamAddedEvent());

        when(sessionEvent('accepted').and(sessionOriginator('remote')))
            .then(updateSessionEndpointData())
            .and(sendAcceptedEvent());

        when(sessionEvent('addstream'))
            .then(sendRemoteStreamAddedEvent());

        when(sessionEvent('confirmed'))
            .then(switchState(SessionState.ESTABLISHED));

        when(sessionEvent('failed'))
            .then(switchState(SessionState.FAILED))
            .and(sendFailedEvent());
    }

    answer(options?: AnswerOptions): void {
        let session = this.fsm.session;

        if (!!options.eventHandlers) {
            session.eventHandlers = options.eventHandlers;
        }

        let answerOptions = {
            pcConfig: {
                iceServers: ICE_SERVERS
            },
            extraHeaders: [
                `${ICE_EXCHANGE_HEADER}: async`
            ]
        };

        if (!!options && !!options.mediaConstraints) {
            let audio = true;
            let video = true;
            if (options.mediaConstraints.hasOwnProperty('audio')) {
                audio = options.mediaConstraints.audio;
            }

            if (options.mediaConstraints.hasOwnProperty('audio')) {
                video = options.mediaConstraints.video;
            }

            answerOptions['mediaConstraints'] = {
                audio: audio,
                video: video
            }
        }

        if (!!options && !!options.receiveMediaConstraints) {
            let audio = true;
            let video = true;

            if (options.receiveMediaConstraints.hasOwnProperty('audio')) {
                audio = options.receiveMediaConstraints.audio;
            }

            if (options.receiveMediaConstraints.hasOwnProperty('video')) {
                video = options.receiveMediaConstraints.video;
            }

            let rtcAnswerConstraints = getCrossBrowserRtcAnswerConstraints({
                offerToReceiveAudio: audio ? 1 : 0,
                offerToReceiveVideo: video ? 1 : 0
            });

            // temasys plugin doesn't like this
            //answerOptions['rtcAnswerConstraints'] = rtcAnswerConstraints;
        }

        if (!!options && !!options.mediaStream) {
            answerOptions['mediaStream'] = options.mediaStream;
        }

        debug('answer options: ', answerOptions);
        session.session.answer(answerOptions);
    }

    terminate() {
        this.fsm.session.session.terminate();
    }
}


