import { State } from "../AuviousState";
import { debug } from "../Debug";
import { uaEvent, connectionFailed } from "../Conditions";
import {
    switchState, sendConnectingEvent, sendConnectedEvent,
    stopPhone, sendConnectionFailedEvent,
    resolveConnectPromise, rejectConnectPromise
} from "../Actions";
import { BaseAuviousState } from "./BaseAuviousState";

export class ConnectingState extends BaseAuviousState {
    readonly state = State.CONNECTING;
    resolve: Function;
    reject: Function;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('connecting state rule setup')

        when(uaEvent('connecting'))
            .and(sendConnectingEvent());
        when(uaEvent('connected'))
            .then(switchState(State.CONNECTED))
            .and(resolveConnectPromise())
            .and(sendConnectedEvent());

        // error handling
        when(connectionFailed())
            .then(switchState(State.DISCONNECTED))
            .and(stopPhone())
            .and(rejectConnectPromise())
            .and(sendConnectionFailedEvent())
    }
}


