import { State } from "../AuviousState";
import { debug } from "../Debug";
import { uaEvent } from "../Conditions";
import { switchState, sendUnregisteredEvent, resolveUnregisterPromise } from "../Actions";
import { BaseAuviousState } from "./BaseAuviousState";

export class UnregisteringState extends BaseAuviousState {
    readonly state = State.UNREGISTERING;
    resolve: Function;
    reject: Function;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('unregistering state rule setup')

        when(uaEvent('unregistered'))
            .then(switchState(State.UNREGISTERED))
            .and(resolveUnregisterPromise())
            .and(sendUnregisteredEvent());
    }

}


