import { AuviousSessionState, SessionState } from "../../AuviousState";
import { SessionImpl } from "../../SessionImpl";
import { debug } from "../../Debug";
import { anyEvent } from "../../Conditions";
import { Rule } from "../../Rule";
import { SessionEventHandlers, HoldInfo, AnswerOptions, Endpoint } from "../../AuviousApi";
import { SessionImplFsm } from "../../SessionImplFsm";
import { print } from '../../Actions';
import { Condition } from "../../Condition";

import { Promise } from 'es6-promise';

export class BaseSessionState implements AuviousSessionState {
    fsm: SessionImplFsm;

    // StateWithRules
    readonly state: string = SessionState.UNKNOWN;
    rules: Rule[];

    constructor(fsm: SessionImplFsm) {
        this.fsm = fsm;

        this.rules = [];
        this.setupRules((condition: Condition): Rule => {
            let rule: Rule = new Rule(condition);
            this.rules.push(rule);
            return rule;
        });
    }

    setupRules(when: Function): void {
        debug('base session rules setup');

        when(anyEvent())
            .then(print());
    }

    get session(): any {
        return this.fsm.session.session;
    }

    // Session
    get id(): string {
        return this.fsm.session.id;
    }

    get eventHandlers(): SessionEventHandlers {
        return this.fsm.session.eventHandlers;
    }

    get direction(): string {
        return !!this.session ? this.session.direction : "unknown";
    }

    get remoteEndpoint(): Endpoint {
        return this.fsm.session.remoteEndpointData;
    }

    isInProgress(): boolean {
        return !!this.session && this.session.isInProgress();
    }

    isEstablished(): boolean {
        return !!this.session && this.session.isEstablished();
    }

    isOnHold(): HoldInfo {
        if (!!this.session) {
            return this.session.isOnHold();
        }

        return { local: false, remote: false };
    }

    hold(): Promise<void> {
        throw new Error("Invalid State");
    }

    unhold(): Promise<void> {
        throw new Error("Invalid state");
    }

    answer(options?: AnswerOptions): void {
        throw new Error('Invalid state.');
    }

    terminate(): void {
        throw new Error('Invalid state.');
    }

    on(eventId: string, handler: Function) {
        this.eventHandlers[eventId] = handler;
    }
}


