import { Action } from "./Action";
import { Event, SessionEvent } from './Event';
import { SessionState } from "./AuviousState";
import { SessionImpl } from "./SessionImpl";
import { Session, Stream, StreamType, Endpoint } from "./AuviousApi";
import { AuviousStateContainer } from "./AuviousStateContainer";
import { StreamImpl } from "./StreamImpl";
import { debugEvent } from "./Debug";
import { uriToEndpoint } from './Common';

export function switchState(state: string): Action {
    return new class {
        run(event: Event): void {
            let session = event.data().eventSession as SessionImpl;

            if (!session) {
                throw new Error("session not found in event: " + event.type());
            }

            session.fsm.switchState(state);
        }
    }
}

export function sendFailedEvent(): Action {
    return new class {
        run(event: Event): void {
            let session = event.data().eventSession as SessionImpl;
            let eventData = event.data().eventData;

            let error = new Error(eventData.cause);
            debugEvent('session failed', error);

            if (!!session.eventHandlers.failed) {
                session.eventHandlers.failed(error);
            }
        }
    }
}

export function sendRingingEvent(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof SessionEvent)) {
                throw new Error("Invalid Event");
            }

            let session: Session = event.data().eventSession;

            debugEvent('session ringing');

            if (!!session.eventHandlers.ringing) {
                session.eventHandlers.ringing();
            }
        }
    }
}

export function sendLocalStreamAddedEvent(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof SessionEvent)) {
                throw new Error("Invalid Event");
            }

            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            let session = event.eventSession as SessionImpl;
            let mediaStream = session.session.localMediaStream as MediaStream;
            let endpoint: Endpoint = { username: stateContainer.identity, endpoint: stateContainer.endpoint };
            let stream: Stream = new StreamImpl('local', getStreamType(mediaStream), endpoint);
            stream.mediaStream = mediaStream;

            if (!!session.localStream) {
                debugEvent('session streamRemoved', session.localStream);
                if (!!session.eventHandlers.streamRemoved) {
                    session.eventHandlers.streamRemoved(session.localStream);
                }
            }

            session.localStream = stream;

            debugEvent('session streamAdded', stream);

            if (!!session.eventHandlers.streamAdded) {
                session.eventHandlers.streamAdded(stream);
            }
        }
    }
}

export function sendRemoteStreamAddedEvent(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof SessionEvent)) {
                throw new Error("Invalid Event");
            }

            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            let session = event.data().eventSession as SessionImpl;
            let mediaStream: MediaStream = event.data().eventData.stream as MediaStream;
            let stream: Stream = new StreamImpl('remote', getStreamType(mediaStream), session.remoteEndpoint);
            stream.mediaStream = mediaStream;

            if (!!session.remoteStream) {
                debugEvent('session streamRemoved', session.remoteStream);
                if (!!session.eventHandlers.streamRemoved) {
                    session.eventHandlers.streamRemoved(session.remoteStream);
                }
            }

            session.remoteStream = stream;

            debugEvent('session streamAdded', stream);

            if (!!session.eventHandlers.streamAdded) {
                session.eventHandlers.streamAdded(stream);
            }
        }
    }
}

export function updateSessionEndpointData(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof SessionEvent)) {
                throw new Error("Invalid Event");
            }

            let session = event.eventSession as SessionImpl;
            let response = event.eventData.response;

            session.remoteEndpointData.endpoint = uriToEndpoint(response.headers['Contact'][0].parsed.uri);
        }
    }
}

export function sendAcceptedEvent(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof SessionEvent)) {
                throw new Error("Invalid Event");
            }

            let session: Session = event.data().eventSession;

            debugEvent('session accepted');

            if (!!session.eventHandlers.accepted) {
                session.eventHandlers.accepted();
            }
        }
    }
}

export function sendEndedEvent(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof SessionEvent)) {
                throw new Error("Invalid Event");
            }

            let stateContainer: AuviousStateContainer = event.data().stateContainer;
            let session = event.data().eventSession;

            debugEvent('session ended');

            if (!!session.eventHandlers.ended) {
                session.eventHandlers.ended();
            }
        }
    }
}

export function setupOnAddStream(): Action {
    return new class {
        run(event: Event): void {
            if (!(event instanceof SessionEvent)) {
                throw new Error("Invalid Event");
            }

            let connection = event.data().eventData.peerconnection;
            let stateContainer = event.stateContainer;
            let session = event.eventSession as SessionImpl;

            connection.onaddstream = (ev) => {
                let eventData = { stream: ev.stream };
                session.fsm.handle(new SessionEvent('addstream', eventData, session, stateContainer));
            };
        }
    }
}


/** helper functions */
function getStreamType(stream: MediaStream): string {
    if (stream.getAudioTracks().length > 0 && stream.getVideoTracks().length == 0) {
        return StreamType.MIC;
    }

    if (stream.getAudioTracks().length == 0 && stream.getVideoTracks().length > 0) {
        return StreamType.CAM;
    }

    if (stream.getAudioTracks().length > 0 && stream.getVideoTracks().length > 0) {
        return StreamType.VIDEO;
    }

    return StreamType.UNKNOWN;
}


