import { Condition } from './Condition';
import { Event, UAEvent, SessionEvent, ApiEvent } from './Event';
import { AuviousStateContainer } from './AuviousStateContainer';
import {
    CONFERENCE_ID_HEADER, P2P_STREAM_ID_HEADER, CONFERENCE_DOMAIN,
    conferenceFactorySipURI, STREAM_DOMAIN, STREAM_ID_HEADER,
    AUVIOUS_CONFERENCE_CLIENT_HEADER
} from './Common';
import { State } from './AuviousState';
import { debug } from './Debug';

export function apiRequest(request: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof ApiEvent)) {
                return false;
            }

            return event.eventId == request;
        }
    });
}

export function anyEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return true;
        }
    })
}

export function anyApiEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof ApiEvent;
        }
    });
}

export function anyUAEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof UAEvent;
        }
    });
}

export function anySessionEvent(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            return event instanceof SessionEvent;
        }
    });
}


export function uaEvent(eventId: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof UAEvent)) {
                return false;
            }

            return (event as UAEvent).eventId == eventId;
        }
    });
}

export function newOutgoingSession(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof UAEvent)) {
                return false;
            }

            return event.eventId == 'newRTCSession' && event.eventData.originator == 'local' && isNormalCall(event.eventData);
        }
    });
}

export function newIncomingSession(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof UAEvent)) {
                return false;
            }

            return event.eventId == 'newRTCSession' && event.eventData.originator == 'remote' && isNormalCall(event.eventData);
        }
    });
}

export function newMessage(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof UAEvent)) {
                return false;
            }

            return event.eventId == 'newMessage' && event.eventData.originator == 'remote';
        }
    })
}

export function newIncomingConferenceP2PStreamSession(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof UAEvent)) {
                return false;
            }

            if (event.eventId != 'newRTCSession') {
                return false;
            }

            if (event.eventData.originator != 'remote') {
                return false;
            }

            if (!isConferenceStreamP2PSession(event.eventData)) {
                return false;
            }

            let data = event.eventData;
            let stateContainer = event.stateContainer;

            let streamId = data.request.headers[P2P_STREAM_ID_HEADER][0].raw;

            let keys = Object.keys(stateContainer.conferenceSessions);

            for (let i = 0; i < keys.length; i++) {
                let key = keys[i];
                let conferenceSession = stateContainer.conferenceSessions[key];

                if (!!conferenceSession.streams[streamId]) {
                    return true;
                }
            }

            return false
        }
    })
}

export function sessionEvent(eventId: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof SessionEvent)) {
                return false;
            }

            return event.eventId == eventId;
        }
    });
}

export function sessionOriginator(originator: string): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof SessionEvent)) {
                return false;
            }

            return event.eventData.originator == originator;
        }
    });
}

export function registrationFailed(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof UAEvent)) {
                return false;
            }

            return event.eventId == 'registrationFailed';
        }
    });
}

export function disconnected(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof UAEvent)) {
                return false;
            }

            let eventId = event.eventId;

            return eventId == 'disconnected' && !event.eventData;
        }
    });
}

export function connectionFailed(): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof UAEvent)) {
                return false;
            }

            let eventId = event.eventId;
            let code = !!event.eventData ? event.eventData.code : undefined;

            return eventId == 'disconnected' && code == 1006;
        }
    });
}

export function state(state: State): Condition {
    return new Condition({
        matches(event: Event): boolean {
            if (!(event instanceof UAEvent)) {
                return false;
            }

            return event.data().stateContainer.state.state() == state;
        }
    });
}

export function targetState(state: State): Condition {
    return new Condition({
        matches(event: Event): boolean {
            let stateContainer = event.data().stateContainer;

            if (!stateContainer) {
                throw new Error("stateContainer not found in event: " + event.type());
            }

            return stateContainer.targetState.state() == state;
        }
    });
}

/* helper functions */
function isNormalCall(data: any) {
    return !isConferenceFactorySession(data)
        && !isConferenceStreamP2PSession(data)
        && !isConferenceSession(data)
        && !isConferenceViewStreamSession(data)
        && !isConferencePublishStreamSession(data)
}

function isConferenceSession(data: any): boolean {
    let extraHeaders = data.request.extraHeaders as Array<string>;

    return !!extraHeaders && extraHeaders
        .filter(header => header.match(`^${AUVIOUS_CONFERENCE_CLIENT_HEADER}: .*`))
        .length > 0
}

function isConferenceFactorySession(data: any): boolean {
    return data.request.ruri.toString() == conferenceFactorySipURI()
}

function isConferenceStreamP2PSession(data: any): boolean {
    return !!data.request.headers[P2P_STREAM_ID_HEADER];
}

function isConferenceViewStreamSession(data: any): boolean {
    return data.request.to.uri.host == STREAM_DOMAIN;
}

function isConferencePublishStreamSession(data: any): boolean {
    let extraHeaders = data.request.extraHeaders as Array<string>;

    if (!extraHeaders) {
        return false;
    }

    for (let i = 0; i < extraHeaders.length; i++) {
        let parts = extraHeaders[i].split(":");
        let header = parts[0];
        if (header == STREAM_ID_HEADER) {
            return true;
        }
    }

    return false;
}




