import { BaseConferenceSessionState } from "./BaseConferenceSessionState";
import { ConferenceSessionState } from "../../AuviousState";
import { debug } from "../../Debug";
import { joinConferenceSessionEvent } from "../../ConferenceConditions";
import { switchState, sendJoinConferenceConnectingEvent, sendJoinConferenceFailedEvent } from "../../ConferenceActions";

export class InitializedConferenceSessionState extends BaseConferenceSessionState {
    state = ConferenceSessionState.INITIALIZED;

    setupRules(when: Function): void {
        super.setupRules(when);
        debug('initialized conference session state rule setup');

        when(joinConferenceSessionEvent('connecting'))
            .then(sendJoinConferenceConnectingEvent());

        when(joinConferenceSessionEvent('progress'))
            .then(switchState(ConferenceSessionState.IN_PROGRESS));

        when(joinConferenceSessionEvent('failed'))
            .then(switchState(ConferenceSessionState.FAILED))
            .and(sendJoinConferenceFailedEvent())
    }
}


