import {
    AuviousApi, ConnectOptions, RegisterOptions, CallOptions, StreamType,
    Conference, ConferenceMode, ConferenceSession,
    JoinConferenceOptions, CreateConferenceOptions, Stream
} from './AuviousApi';
import { AuviousState } from './AuviousState';
import { AuviousStateContainer } from './AuviousStateContainer';

import { Promise } from 'es6-promise';

export class Auvious implements AuviousApi {
    private stateContainer: AuviousStateContainer;

    public constructor() {
        this.stateContainer = new AuviousStateContainer();
    }

    public connect(options: ConnectOptions): Promise<void> {
        return this.state().connect(options);
    }

    public disconnect(): Promise<void> {
        return this.state().disconnect();
    }

    public register(options: RegisterOptions): Promise<void> {
        return this.state().register(options);
    }

    public unregister(): Promise<void> {
        return this.state().unregister();
    }

    public connecting(): boolean {
        return this.state().connecting();
    }

    public connected(): boolean {
        return this.state().connected();
    }

    public registered(): boolean {
        return this.state().registered();
    }

    public identity(): string {
        return this.state().identity();
    }

    public endpoint(): string {
        return this.state().endpoint();
    }

    // one on one calling
    public call(username: string, options?: CallOptions): void {
        this.state().call(username, options);
    }

    // conference calling
    public createConference(options?: CreateConferenceOptions): Promise<Conference> {
        return this.state().createConference(options);
    }

    // join conference
    public joinConference(options: JoinConferenceOptions): Promise<ConferenceSession> {
        return this.state().joinConference(options);
    }

    // message a user
    public message(username: string, message: string): Promise<void> {
        return this.state().message(username, message);
    }

    // set event handler
    public on(event: string, handler: Function) {
        this.state().on(event, handler);
    }

    private state(): AuviousState {
        return this.stateContainer.state;
    }
}



